import { newE2EPage } from '@stencil/core/testing';

describe('presto-muted-overlay', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-muted-overlay></presto-muted-overlay>');

    const element = await page.find('presto-muted-overlay');

    expect(element).toHaveClass('hydrated');
  });
});
